.footer-section {
	&__content {
		padding-top: 0px;
		background: #33353A; }
	&__inner {}
	&__imgs {
		padding-top: 87px;
		height: 224px;
		background: #ffffff;

		&-inner {
			display: flex;
			align-items: flex-end;
			// justify-content: center
			justify-content: space-between;
			padding-left: 101px; } }
	&__img {
		position: relative;
		// &:not(:last-of-type)
		// 	margin-right: 150px
		&--motors {

			top: 20px; }
		&--energy {
			top: 11px; } }
	&__text-block {
		padding-top: 255px;
		color: #fff;
		font-size: 32px;
		line-height: 1.06;
		h3 {
			@include hm-sb;
			font-size: 32px;
			line-height: 1.06;
			margin-bottom: 10px; } }
	&__info-group {
		&:not(:last-of-type) {
			margin-bottom: 54px; }
		a {
			color: #fff;
			text-decoration: none;
			span {
				color: $orange; } }
		p {
			&:not(:last-of-type) {
				margin-bottom: 10px; } } } }
